.item {
    box-shadow: 3px 2px #00000027;
}
.item_img {
    object-fit: contain;
    width: 100%;
}

.item:hover {
    cursor: pointer;
    transform: scale(1.01);
}
.item_content {
    padding: 8px;
    display: flex;
    flex-direction: column;
    background-color: white;
    align-items: flex-start;
}
.item_title {
    text-align: start;
    font-weight: 500;
    color: black;
    font-size: 14px;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
}
.item_prices {
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-weight: 600;
    align-items: center;
}
.item_price{
    color: black;
    font-size: 15px;
}
.item_quanity {
    font-size: 11px;
}