.product {
    display: flex;
    flex-direction: row;
}
.product_img {
    width: 450px;
    object-fit: contain;
}
.product_name {
    display: -webkit-box;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    font-weight: 500;
    margin: 0;
    vertical-align: sub;
    max-height: 3rem;
    line-height: 1.5rem;
    overflow: hidden;
    max-width: 41.5625rem;
    font-size: 1.25rem;
    word-wrap: break-word;
}
.product_price {
    font-size: 30px;
}
.product_info {
    margin-left: 30px;

}
.product_quanitybutton {
    outline: none;
    cursor: pointer;
    border: 0;
    font-size: 22px;
    font-weight: 300;
    line-height: 1;
    letter-spacing: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color .1s cubic-bezier(.4,0,.6,1);
    border: 1px solid rgba(0,0,0,.09);
    background: transparent;
    color: rgba(0,0,0,.8);
    width: 32px;
    height: 32px;
}
.product_count {
    outline: none;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background-color .1s cubic-bezier(.4,0,.6,1);
    border: 1px solid rgba(0,0,0,.09);
    background: transparent;
    color: rgba(0,0,0,.8);
    width: 32px;
    height: 30px;

}
.product_addtocart {
    height: 48px;
    background-color: #333333d9;
    color: white;
    padding: 20px;
    border: 1px solid ;
    cursor: pointer;
    border-radius: 2px;
    font-size: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.product_addtocart:hover ,
.product_btnBuy:hover {
    background-color: rgba(0,0,0,0.5)
}
.product_btnBuy {
    font-size: 16px;
    height: 48px;
    cursor: pointer;
    border: none;
    border-radius: 2px;
    padding: 0px 40px;
    background-color: black;
    color: white;
    margin-left: 20px;
}